        modifier = modifier,
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        Image(
            modifier = Modifier.size(MaterialTheme.dimens.iconSize.mentoryBenefitItemIconSize),
            painter = painterResource(resource),
            contentDescription = null
